import React, { useState, useEffect, useContext } from "react";
import styles from "../../styles/Service.module.css";
import { useParams } from "react-router-dom";
import ServiceType from "../../components/ServiceType";
import { ServicesContext } from "../../context/ServicesContext";

const Service = () => {
  const params = useParams();
  const { services, setservices } = useContext(ServicesContext);
  const [service, setservice] = useState({ categories: [] });
  const [categories, setCategories] = useState([]);
  const [saveToDb, setSaveToDb] = useState(false);

  useEffect(() => {
    const currentService = services?.filter(
      (s) => s.number === parseInt(params.id)
    )[0];
    setCategories(currentService.categories);
    setservice(currentService);
  }, [services, params.id]);

  const handleAddType = () => {
    let ctgData = {
      name: "",
      data: "",
      number: !service.categories.length ? 1 : service.categories.length + 1,
    };
    setservice({
      ...service,
      categories: [...service.categories, ctgData],
    });
  };

  const handleDelete = (num) => {
    setservice({
      ...service,
      categories: service.categories.filter((c) => c.number !== num),
    });
  };

  const handleSaveService = (params) => {
    setSaveToDb(true);
    setservices([
      ...services.filter((s) => s.number !== service.number),
      service,
    ]);
  };

  const handleUpdate = (name, data, number) => {
    let ctgData = {
      name,
      data,
      number,
    };
    console.log(ctgData);
    setservice({
      ...service,
      categories: [
        ...service.categories.filter((c) => c.number !== number),
        ctgData,
      ],
    });
  };

  return (
    <div className={styles.service_wrapper}>
      <div className={styles.service_header}>
        <h2 className={styles.service_title}>{service?.service?.title}</h2>
        <div className={styles.header_actions}>
          <button
            onClick={handleSaveService}
            className='glb_btn'
            style={{ marginRight: "0.5rem" }}
          >
            Saqlash
          </button>
          <button onClick={handleAddType} className='glb_btn'>
            <i class='bx bx-plus bx-sm'></i>
          </button>
        </div>
      </div>
      {service.categories.map((c) => {
        return (
          <ServiceType
            saveToDb={saveToDb}
            handleDelete={handleDelete}
            category={c}
            handleUpdate={handleUpdate}
          />
        );
      })}
    </div>
  );
};

export default Service;
