import React, { useContext, useState } from "react";
import { Link } from "react-router-dom";
import ServiceCard from "../../components/ServiceCard";
import NewsSection from "../../components/NewsSection";
import styles from "../../styles/Home.module.css";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import staff from "../../assets/staff.json";
import { ServicesContext } from "../../context/ServicesContext";
import { NewsContext } from "../../context/NewsContext";
import Modal from "../../components/Modal";

const settings = {
  dots: true,
  infinite: true,
  speed: 500,
  slidesToShow: 1,
  slidesToScroll: 1,
  autoplay: true,
  speed: 1000,
  autoplaySpeed: 2000,
};

const Home = () => {
  const { services, setservices } = useContext(ServicesContext);
  const { news } = useContext(NewsContext);
  const [open, setopen] = useState(false);

  const handleAddService = () => {
    setservices([
      ...services,
      {
        name: "Unknown name",
        categories: [],
        number: !services.length ? 1 : services.length + 1,
      },
    ]);
    console.log(services);
  };

  return (
    <div className={styles.home_contents_wrapper}>
      <Modal open={open} setopen={setopen} />
      <div className={styles.content1}>
        <section className={styles.ourstaff_section}>
          <div>
            <Slider {...settings}>
              {staff.map((s) => {
                return (
                  <div>
                    <div className={styles.staff_wrapper}>
                      <div
                        className={styles.staff_img}
                        style={{ backgroundImage: `url(${s.pic})` }}
                      ></div>
                      <div className={styles.staff_about}>
                        <h3>{`${s.fullName}`}</h3>
                        <p>{s.about}</p>
                      </div>
                    </div>
                  </div>
                );
              })}
            </Slider>
          </div>
        </section>
        <section className={styles.services_section}>
          <div className='section_title_wrapper'>
            <h3 className='section_title'>Bizning xizmatlar</h3>
            <button className='glb_btn' onClick={handleAddService}>
              <i class='bx bx-plus-circle bx-sm'></i>
            </button>
          </div>
          <div className={styles.services}>
            {services.map((s) => (
              <Link to={`/service/${s.number}`}>
                <ServiceCard data={s} />
              </Link>
            ))}
          </div>
        </section>
      </div>
      <div className={styles.news}>
        <div className='section_title_wrapper'>
          <h3 className='section_title'>Huquqiy yangiliklar</h3>
          <button className='glb_btn' onClick={() => setopen(true)}>
            <i class='bx bx-plus-circle bx-sm'></i>
          </button>
        </div>

        {news.map((n) => (
          <NewsSection news={n} />
        ))}
      </div>
      <button className='glb_btn send_question_btn'>
        Savol yuborish
        <i class='bx bxs-chevron-right'></i>
      </button>
    </div>
  );
};

export default Home;
